import React, { useEffect, useState } from 'react';
import { DetailsList, DetailsListLayoutMode, IColumn, SelectionMode } from '@fluentui/react';
import { useQuery } from 'react-query';
import { optionDataProvider } from '../DataProvider/OptionDataProvider';

interface UserInfoTableProps {
    indexSymbol: string
    etfSymbol: string
}

const UserInfoTable: React.FC<UserInfoTableProps> = ({ indexSymbol, etfSymbol }) => {

    const [items, setItems] = useState<any[]>([
        { key: 'index', accountType: '', equity: '', availableFunds: '', riskLevel: '' },
        { key: 'etf', accountType: 'ETF', equity: '', availableFunds: '', riskLevel: '' },
        { key: 'row3', accountType: '', equity: '', availableFunds: '', riskLevel: '' },
    ]);

    // Define the columns for the table
    const columns: IColumn[] = [
        { key: 'column1', name: '', fieldName: 'accountType', minWidth: 100, maxWidth: 150, isRowHeader: true },
        // { key: 'column2', name: '', fieldName: 'equity', minWidth: 150, maxWidth: 200 },
        // { key: 'column3', name: '', fieldName: 'availableFunds', minWidth: 150, maxWidth: 200 },
        { key: 'column4', name: '', fieldName: 'riskLevel', minWidth: 100, maxWidth: 150 },
    ];

    const { data: indexRiskLevel } = useQuery(
        ['indexOptionMonitor', indexSymbol],  // symbol  keysymbol 
        () => optionDataProvider.fetchIndexOptionMonitor(indexSymbol as string),
        {
            onSuccess(data) {
                console.log('fetchIndexOptionMonitor: ' + JSON.stringify(data))
            },
            enabled: !!indexSymbol,  //  symbol 
            refetchOnWindowFocus: false, // 
            refetchInterval: 3000, //  3 
        }
    );

    const { data: etfRiskLevel } = useQuery(
        ['etfOptionMonitor', etfSymbol],  // symbol  keysymbol 
        () => optionDataProvider.fetchETFOptionMonitor(etfSymbol as string),
        {
            onSuccess(data) {
                console.log('fetchETFOptionMonitor: ' + JSON.stringify(data))
            },
            enabled: !!etfSymbol,  //  symbol 
            refetchOnWindowFocus: false, // 
            refetchInterval: 3000, //  3 
        }
    );

    useEffect(() => {
        const updatedItems = items.map((item) =>
            item.key === 'index'
                ? { ...item, riskLevel: indexRiskLevel ?? '' } // Update riskLevel for row1
                : item
        );

        console.log("useEffect" + JSON.stringify(updatedItems))

        setItems(updatedItems); // Set the updated array
    }, [indexRiskLevel, items]);

    useEffect(() => {
        const updatedItems = items.map((item) =>
            item.key === 'etf'
                ? { ...item, riskLevel: etfRiskLevel ?? '' } // Update riskLevel for row1
                : item
        );

        setItems(updatedItems); // Set the updated array
    }, [etfRiskLevel, items]);




    return (
        <DetailsList
            items={items}
            columns={columns}
            selectionMode={SelectionMode.none}
            layoutMode={DetailsListLayoutMode.fixedColumns}
            selectionPreservedOnEmptyClick
        />
    );
};

export default UserInfoTable;
